<template>
  <b-table class="pt-3" responsive striped :items="songs" :fields="fields">
    <template v-slot:cell(use)="data">
      <b-form-checkbox size="lg" v-model="data.item.use"></b-form-checkbox>
    </template>
    <template v-slot:cell(hash)="data">
      <b-img-lazy
        :src="'https://scoresaber.com/imports/images/songs/' + data.item.hash + '.png'"
        rounded
        width="48"
        height="48"
      />
    </template>
    <template v-slot:cell(song_name)="data">
      <b-link
        target="_blank"
        :href="'https://beatsaver.com/beatmap/' + data.item.key"
      >{{data.item.song_name}}</b-link>
    </template>
    <template v-slot:cell(accuracy)="data">{{data.item.accuracy.toFixed(2)}}%</template>
    <template v-slot:cell(score_add)="data">
      {{data.item.score.toFixed(2)}}pp
      <sup
        class="text-success"
      >+{{data.item.score_add.toFixed(2)}}pp</sup>
    </template>
  </b-table>
</template>

<script>
export default {
  name: "SongTable",
  props: { songs: Array },
  data: function() {
    return {
      fields: [
        {
          key: "use",
          label: ""
        },
        {
          key: "hash",
          label: ""
        },
        {
          key: "song_name",
          label: "Song",
          sortable: true
        },
        {
          key: "accuracy",
          label: "Target accuracy",
          sortable: true
        },
        {
          key: "score_add",
          label: "Score",
          sortable: true
        }
      ]
    };
  }
};
</script>

<style>
.table > tbody > tr > td {
  vertical-align: middle;
}
</style>