<template>
  <b-form @submit="onSubmit">
    <b-input-group prepend="Link" class="mt-3">
      <b-form-input
        id="input-none"
        v-model="user_id"
        :state="isValid"
        :disabled="isLoading"
        placeholder="Example: https://scoresaber.com/u/76561198078246774"
      ></b-form-input>
      <b-input-group-append>
        <b-button type="submit" :variant="isLoading ? '' : 'primary'">
          <b-spinner v-if="isLoading" small></b-spinner>
          {{isLoading ? "Loading..." : "Recommend"}}
        </b-button>
      </b-input-group-append>
    </b-input-group>
  </b-form>
</template>

<script>
export default {
  name: "Search",
  props: { isLoading: Boolean },
  data: function() {
    return { user_id: "" };
  },
  methods: {
    onSubmit: function(evt) {
      evt.preventDefault();
      if (this.isValid) {
        this.$emit("search", this.parsedUserId);
      }
    }
  },
  computed: {
    isValid: function() {
      if (this.user_id.length < 1) return null;
      if (!this.user_id.includes("scoresaber.com")) return false;
      return !isNaN(Number(this.parsedUserId));
    },
    parsedUserId: function() {
      return this.user_id
        .split("/")
        .pop()
        .split("&")[0]
        .split("?")[0];
    }
  }
};
</script>