<template>
  <b-container>
    <div class="pt-5 text-center">
      <img class="d-block mx-auto mb-4" src="bs-icon.png" alt width="72" height="72" />
      <h2>Beat Helper</h2>
      <p class="lead">
        Enter your
        <a href="https://scoresaber.com/global" target="_blank">Score Saber</a> link to get recomendations
      </p>
      <Search :is-loading="loading" v-on:search="request"></Search>
    </div>

    <div class="pt-3 text-center">
      <b-alert v-model="error" variant="danger" dismissible>{{error_msg}}</b-alert>
    </div>

    <div v-if="loading && long_loading" class="pt-3 text-center">
      <p class="lead">Please wait. Making a playlist can take up to a minute...</p>
    </div>

    <div v-if="songs.length" class="pt-3">
      <b-row>
        <b-col class="text-center">
          <p class="lead">
            Found {{maybePluralize(songs.length, "song")}} for
            <b-link :href="'https://scoresaber.com/u/' + user_id" target="_blank">{{user_name}}</b-link>. Check songs you want and download playlist.
          </p>
        </b-col>
        <b-col sm="2" class="text-right">
          <Download :songs="songs"></Download>
        </b-col>
      </b-row>
      <SongTable :songs="songs"></SongTable>
    </div>

    <TimeFooter></TimeFooter>
  </b-container>
</template>

<script>
import Search from "./components/Search";
import SongTable from "./components/SongTable";
import TimeFooter from "./components/TimeFooter";
import Download from "./components/Download";

async function getPlaylist(user_id) {
  try {
    let response = await fetch(`http://163.172.157.20:22555/player/${user_id}`);
    let data = await response.json();
    return data;
  } catch (err) {
    return { status: "error", error: err };
  }
}

export default {
  name: "Main",
  components: {
    Search,
    SongTable,
    TimeFooter,
    Download
  },
  data: function() {
    return {
      user_id: "",
      user_name: "",
      loading: false,
      long_loading: false,
      error_msg: "",
      error: false,
      songs: []
    };
  },
  methods: {
    request: function(uid) {
      //Parse user ID
      this.songs = [];
      this.user_id = uid;
      this.error = false;
      this.loading = true;
      this.long_loading = false;
      getPlaylist(this.user_id).then(resp => this.onResponce(resp));
      setTimeout(() => (this.long_loading = true), 4000);
    },
    onResponce: function(resp) {
      this.loading = false;
      this.long_loading = false;
      if (resp.status === "error") {
        this.error = true;
        this.error_msg = resp.error;
      } else {
        this.user_name = resp.name;
        for (let i = 0; i < resp.songs.length; ++i) {
          resp.songs[i].use = true;
          resp.songs[i].score_add =
            resp.songs[i].score - resp.songs[i].your_score;
        }
        this.songs = resp.songs;
      }
    },
    maybePluralize: function(count, noun, suffix = "s") {
      return `${count} ${noun}${count !== 1 ? suffix : ""}`;
    }
  }
};
</script>