<template>
  <footer class="my-5 pt-5 text-muted text-center text-small">
    <p v-if="time_string.length > 0" class="mb-1">Last database update was {{time_string}}</p>
    <p class="mb-1">&copy; 2020 Norne</p>
  </footer>
</template>

<script>
async function getTime() {
  try {
    let response = await fetch("http://163.172.157.20:22555/player/update");
    let data = await response.json();
    return data.last_update;
  } catch (err) {
    return 1586522756.7299583;
  }
}

export default {
  name: "TimeFooter",
  data: function() {
    return {
      time_string: ""
    };
  },
  mounted: function() {
    getTime().then(time => {
      var newDate = new Date();
      newDate.setTime(time * 1000);
      this.time_string = newDate.toLocaleString();
    });
  }
};
</script>