<template>
  <b-button :disabled="!loaded" variant="success" v-on:click="download_playlist">Download</b-button>
</template>

<script>
function download(filename, text) {
  var element = document.createElement("a");
  element.setAttribute(
    "href",
    "data:text/plain;charset=utf-8," + encodeURIComponent(text)
  );
  element.setAttribute("download", filename);

  element.style.display = "none";
  document.body.appendChild(element);

  element.click();

  document.body.removeChild(element);
}

async function getImage() {
  try {
    let response = await fetch("/b64image.txt");
    let data = await response.text();
    return data;
  } catch (err) {
    return "";
  }
}

export default {
  name: "Download",
  props: { songs: Array },
  data: function() {
    return {
      b64image: "",
      loaded: false
    };
  },
  methods: {
    download_playlist: function() {
      let playlist = {
        playlistTitle: "Top PP songs",
        playlistAuthor: "Norne",
        image: this.b64image,
        songs: []
      };
      this.songs.forEach(song => {
        if (!song.use) return;
        playlist.songs.push({
          key: song.key,
          hash: song.hash,
          songName: song.song_name,
          uploader: song.uploader
        });
      });
      download("top.bplist", JSON.stringify(playlist));
    }
  },
  mounted: function() {
    getImage().then(image => {
      this.b64image = image;
      this.loaded = true;
    });
  }
};
</script>